@import '~bpk-mixins/index';

$bpk-spacing-v2: true;

@mixin fullscreen() {
  display: flex;
  padding: 0;
}

.bpk-modal__container {
  display: flex;
  padding: bpk-spacing-lg();

  // IE11 and below hack for buggy flexbox support
  @media screen\0 {
    display: block;
  }

  &--full-screen-mobile {
    @include bpk-breakpoint-mobile {
      @include fullscreen;
    }
  }

  &--full-screen {
    @include fullscreen;
  }
}
